import Vue from 'vue';
import VueRouter, { RouteConfig } from 'vue-router';
import Layout from '@/components/layout/index.vue';

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: '/',
    redirect: '/home',
    component: Layout,
    children: [
      {
        path: 'home',
        name: 'home',
        component: () =>
          import(/* webpackChunkName: "home" */ '@/views/home/index.vue'),
      },
      {
        path: 'homeMore',
        name: 'homeMore',
        component: () => import('@/views/home/TestMore.vue'),
      },
      {
        path: 'homeDetails',
        name: 'homeDetails',
        component: () => import('@/views/home/details.vue'),
      },
      {
        path: 'business',
        name: 'business',
        component: () => import('@/views/business/index.vue'),
      },
      {
        path: 'sharing',
        name: 'sharing',
        component: () => import('@/views/sharing/index.vue'),
      },
      {
        path: 'device',
        name: 'device',
        component: () => import('@/views/device/index.vue'),
      },
      {
        path: 'leanProduction',
        name: 'leanProduction',
        component: () => import('@/views/leanProduction/index.vue'),
      },
      {
        path: 'productionLine',
        name: 'productionLine',
        component: () => import('@/views/productionLine/index.vue'),
      },
      {
        path: 'productionLine',
        name: 'productionLine',
        component: () => import('@/views/productionLine/index.vue'),
      },
      {
        path: 'repair',
        name: 'repair',
        component: () => import('@/views/repair/index.vue'),
      },
      {
        path: 'idea',
        name: 'idea',
        component: () => import('@/views/idea/index.vue'),
      },
      {
        path: 'technician',
        name: 'technician',
        component: () =>
          import(
            /* webpackChunkName: "technician" */ '@/views/technician/index.vue'
          ),
      },
      {
        path: 'technology',
        name: 'technology',
        component: () =>
          import(/* webpackChunkName: "list" */ '@/views/technology/index.vue'),
      },
      {
        path: 'detail',
        name: 'detail',
        component: () =>
          import(/* webpackChunkName: "detail" */ '@/views/detail/index.vue'),
      },
      {
        path: 'guarantee',
        name: 'guarantee',
        component: () =>
          import(
            /* webpackChunkName: "guarantee" */ '@/views/guarantee/index.vue'
          ),
      },
    ],
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
