import Vue from 'vue';
import App from './App.vue';
import router from './router';
import {
  Button,
  Layout,
  Menu,
  Table,
  FormModel,
  Input,
  DatePicker,
  Pagination,
  Select,
  ConfigProvider,
  Checkbox,
} from 'ant-design-vue';

Vue.config.productionTip = false;

Vue.use(Button)
  .use(Layout)
  .use(Menu)
  .use(Table)
  .use(FormModel)
  .use(Input)
  .use(DatePicker)
  .use(Pagination)
  .use(ConfigProvider)
  .use(Checkbox)
  .use(Select);

new Vue({
  router,
  render: (h) => h(App),
}).$mount('#app');
